import {Colors} from "../styles/Colors.js";
import styled from "styled-components";

export const NavPc = styled.nav`
    position:fixed;
    right:5%;
    top:35%;
    text-align:right;
    z-index:30;
   
`;
export const Homeoption = styled.p`
    font-family:"TMC";
    color:${Colors.letters};
    font-size:20px;
    margin-bottom:40%;
    cursor: pointer;
    &:hover{
        color:${Colors.main}
    }
    &:after{
        content:"";
        position: fixed;
        height:0.5%;
        width:3%;
        background-color:${Colors.main};
        margin-top:.8%;
        margin-left:1%;
        border-radius:20px;
        display:${props => props.Home ? "inline-block" :"none" };
    }
`;
