import React, {useRef, useEffect} from "react";
import {OptionsNav} from "../styles/MenuOptionsStyles";
import anime from "animejs"
import ProjectsPage from "./ProjectsPage";
const MenuOptions = ({clickMenu, Home,Projects,About , setclickMenu}) => {
    const Navref = useRef();
    const handleScroll = (e) => {
        setclickMenu(!true)
        const id = e.currentTarget.id
        if(id=="Home"){
            Home.current.scrollIntoView({ behavior: 'smooth', block: 'start' })
        }else if(id=="Projects"){
            Projects.current.scrollIntoView({ behavior: 'smooth', block: 'start' })
        }else if(id=="About"){
            About.current.scrollIntoView({ behavior: 'smooth', block: 'start' })
        }
    }
    useEffect(()  => {
        clickMenu ? 
        anime({
            targets: Navref.current,
            opacity: [0,1],
            translateX: "-100%",
            duration: 500,
            easing: 'easeInOutBack'
        }):
        anime({
            targets: Navref.current,
            opacity: [1, 0],
            translateX: "0",
            duration: 500,
            easing: 'easeInBack'
        })
    },[clickMenu])
    return(
        <OptionsNav ref={Navref}>
            <p id="Home" onClick={handleScroll}>
                Home
            </p>
            <p id="Projects" onClick={handleScroll}>
                Projects
            </p>
            <p id="About" onClick={handleScroll}>
                About me
            </p>
            <p>
                Contact me
            </p>
        </OptionsNav>
        )
}
export default MenuOptions;