import React,{useEffect} from "react";
import {NavPc, Homeoption,Projectsoption,Aboutoption} from "../styles/MenuPCStyles"
import ProjectsPage from "./ProjectsPage";
import {useInViewport} from "./useInViewport";
const MenuPC = ({Home, Projects, About}) => {
    const handleScroll = (e) => {
        const id = e.currentTarget.id
        if(id=="Home"){
            Home.current.scrollIntoView({ behavior: 'smooth', block: 'start' })
        }else if(id=="Projects"){
            Projects.current.scrollIntoView({ behavior: 'smooth', block: 'start' })
        }else if(id=="About"){
            About.current.scrollIntoView({ behavior: 'smooth', block: 'start' })
        }}
    const InviewHome = useInViewport(Home,.5)
    const InviewProjects = useInViewport(Projects,.5)
    const InviewAbout = useInViewport(About,.5)
    return(
        <NavPc>
            <Homeoption id="Home" Home={InviewHome} onClick={handleScroll}>
                Home
            </Homeoption>
            <Projectsoption id="Projects" Projects={InviewProjects} onClick={handleScroll}>
                Projects
            </Projectsoption >
            <Aboutoption id="About" About={InviewAbout} onClick={handleScroll}>
                About me
            </Aboutoption>
            <p>
                Contact me
            </p>
        </NavPc>
    )

}
export default  MenuPC;