import styled from "styled-components";
import { Colors } from "../styles/Colors.js";
import {motion} from "framer-motion";
import Esquina from "../img/Esquina.svg"

export const SectionProjects = styled.section`
    width: 100vw;
    height: 100vh;
    @media (min-width:1200px){
        &:before{
            content:url(${Esquina});
            position:fixed;
            width:107px;
            height:107px;
            bottom:2%;
            left:2%;
            z-index:20;
            transform:rotateZ(270deg);
        }
        &:after{
            content:url(${Esquina});
            position:fixed;
            z-index:20;
            width:107px;
            height:107px;
            bottom:2%;
            right:2%;
            transform:rotateZ(180deg);
        }
    }
`;
export const ContainerProjects = styled(motion.div)`
    width:90%;
    height: 95%;
    margin: 0 auto;
    display:flex;
    flex-direction:column;
    align-items:center;
    @media (min-width:1200px){
        flex-direction:row;
        width:100%;
        height:100%;

    }

`;
export const TitleProjects = styled(motion.h1)`
    color:${Colors.title};
    font-size:50px;
    font-family:"TMC";
    @media (min-width:768px){
        font-size:70px;
    } 
`;
export const Description = styled.div`
    width:100%;
    height:100%;
    @media (min-width:1200px){
        display: flex;
        align-items:center;
    }
`;
export const ContainerDescription = styled(motion.article)`
    display:flex;
    flex-direction:column;
    align-items:center;
    justify-content:space-between;
    width:100%;
    height:30%;
    p{
        position:relative;
        width:80%;
        font-size:20px;
        margin-top:5%;
        font-family:"TMC";
        color:${Colors.letters};
        line-height:1.5;
        text-align:justify;
    }
    h5{
        font-size:15px;
        font-family:"TMC";
        position:relative;
        top:10%;
        color:${Colors.main}

    }
    @media (min-width:768px){
        p{
            font-size:40px;
        }
        h5{
            font-size:20px;
        }
    }
    @media(min-width:1200px){
        width:50%;
        height:55%;
        position: relative;
        top:-10%;
        left:-1%;
        p{
            font-size:20px;
            position:relative;
            left:15%;
        }
        h5{
            position:relative;
            left:-7%;
        }
    }
`;
export const IMGContainer = styled(motion.a)`
    width:80%;
    height:100%;
    img{
        position:relative;
        top:20%;
        width:100%;
        border-radius:10%;
        border: 1px solid ${Colors.main}
    }
    @media (min-width:768px){
        width:75%;
        height:75%;
        img{
            left:17.25%;
        }
    }
    @media (min-width:1200px){
        width:30%;
        img{
            width:100%;
            top:0;
            z-index:-20;
        }
        z-index:0;
    }
`;
export const Title = styled(motion.div)`
    width:95%;
    display:flex;
    align-items:center;
    justify-content:space-around;
    &:before{
        content:"";
        width:15%;
        height:15%;
        background-color:${Colors.main};
        position: relative;
        top:0;
        right:3%;
        display:inline-block;
    }
    span{
        font-family:"TMC";
        color:${Colors.main};
        font-size:35px;
    }
    h3{
        font-size:30px;
        font-family:"TMC";
        color:${Colors.letters}
    }
    @media (min-width:768px){
        width:78%;
        h3{
            font-size:50px;
        }
        span{
            font-size:50px;
        }
    }
    @media (min-width:1200px){
        width:100%;
        position: relative;
        left:10%;
        display:inline-block;
        z-index:30;
        span{
            font-size:64px;
            position: relative;
            left:20%;
            
        }
        h3{
            width:200%;
            position: relative;
            font-size:80px;
            left:15%;
        }
        &:before{
            width:10%;
            left:15%;
            height:5%;
            top:-10%;
        }

    }
`;